.submit {
  padding: 0 20px 32px 20px;
  text-align: center;
}

.submit__button {
  min-width: 280px;
  height: 40px;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 21px;
}

.submit__legend {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
  font-weight: 400;
  padding: 0;
  margin: 0;
}
