.gift {
  display: flex;
  justify-content: center;
  position: relative;
  text-align: center;
  box-sizing: border-box;
  margin-top: 35px;
  z-index: 2;
  background-color: rgba(104, 183, 56, 0.85);

  &::before {
    content: "";
    width: 78px;
    height: 80px;
    top: 59px;
    position: absolute;
    background-image: url("../../../img/icon-gift.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &::after {
    content: "";
    position: absolute;
    height: 290px;
    top: 0;
    right: 0;
    left: 0;
    background-image: url("../../../img/cat-gift-back.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    opacity: 0.15;
    z-index: -1;
  }
}

.gift__text {
  display: block;
  max-width: 161px;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  font-weight: 400;
  margin-bottom: 43px;
  margin-top: 187px;
  position: relative;
}
